import Foot from '~/components/Foot'
import Head from '~/components/Head'
import './store.scss'
import { List, TextareaItem, Button } from 'antd-mobile';
import { failToast, loadingToast, successToast } from '&/methods'
import { axios } from '&/axios'


export default class AdminStore extends Component {

  constructor(){
    super();
    this.state={
      msg:''
    }
  }

  saveMsg=()=>{
    var text = this.state.msg;
    if(text.length==0){
      failToast('请输入内容',0.8);
      return;
    }
    axios.post('/bookstore/saveMsg',{text})
  }

  componentDidMount(){
    if(this.state.msg.length==0){
      axios.get('/bookstore/getMsg').then(res=>{
        this.setState({
          msg:res.data.text
        })
      })
    }
  }

  render() {
    return (
      <div className='admin_store'>
        <Head title='简介管理'></Head>
        <div className='store_text'>
          <List style={{ fontSize: '12px' }}>
            <TextareaItem
              style={{ fontSize: '12px' }}
              title="内容："
              autoHeight
              placeholder='输入内容'
              labelNumber={3}
              ref={el=>this.msg=el}
              value={this.state.msg}
              onChange={(v)=>{this.setState({msg:v})}}
            />
          </List>
        </div>
        <div style={{margin:'20px 0 70px 0',display:'flex',justifyContent:'center',alignItems:'center'}}>
          <Button style={{width:'50%'}} type='primary' onClick={this.saveMsg}>保存</Button>
        </div>
        <Foot></Foot>
      </div>
    )
  }
}